"Lato\";\r\n  src: url(\"./fonts/lato-bold-webfont.woff2\") format(\"woff2\"),\r\n    url(\"./fonts/lato-bold-webfont.woff\") format(\"woff\");\r\n  font-weight: 700;\r\n  font-style: normal;\r\n}\r\n\r\n/* CSS RESET */\r\n*,\r\n*::before,\r\n*::after {\r\n  box-sizing: border-box;\r\n}\r\n\r\n*:not(dialog) {\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\n::-webkit-scrollbar {\r\n  width: 10px;\r\n}\r\n\r\n::-webkit-scrollbar-track {\r\n  background: #1e1e1e;\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n  background: #444;\r\n  border-radius: 5px;\r\n}\r\n\r\n::-webkit-scrollbar-thumb:hover {\r\n  background: #555;\r\n}\r\n\r\n* {\r\n  font: inherit;\r\n  scrollbar-width: thin;\r\n  scrollbar-color: #444 #1e1e1e;\r\n}\r\n\r\nimg,\r\npicture,\r\nsvg,\r\nvideo {\r\n  display: block;\r\n  max-width: 100%;\r\n}\r\n\r\nsvg {\r\n  pointer-events: none;\r\n}\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4,\r\nh5,\r\nh6 {\r\n  text-wrap: balance;\r\n}\r\n\r\np,\r\nli,\r\nfigcaption {\r\n  text-wrap: pretty;\r\n}\r\n\r\n/* CSS RESET ENDS HERE */\r\n\r\n:root {\r\n  --maxWidthforTexts: 65ch;\r\n  --light-weight: 300;\r\n\r\n  /* Background Color */\r\n  --bg-color: rgb(25, 25, 25);\r\n\r\n  /* Sidebar Colors */\r\n  --sidebar-font-color: #e0e0e0;\r\n  --sidebar-bg-color: rgb(32, 32, 32);\r\n  --sidebar-bg-color-hover: rgb(44, 44, 44);\r\n\r\n  /* Note Header Colors */\r\n  --note-header-font-color: white;\r\n  --note-header-bg-color: transparent;\r\n  --note-header-bg-color-hover: rgb(38, 38, 38);\r\n\r\n  /* Note Body Colors */\r\n  --note-body-bg-color: rgb(38, 38, 38);\r\n  --note-body-font-color: #e0e0e0;\r\n\r\n  /* Icon / Button Colors */\r\n  --menu-icon-color: white;\r\n  --menu-icon-hover: rgb(44, 44, 44);\r\n  --plus-button-color: transparent;\r\n  --plus-button-color-hover: rgb(44, 44, 44);\r\n  --project-add-button-bg-color: rgb(67, 67, 67);\r\n  --todo-three-dots-bg-color-hover: rgb(50, 50, 50);\r\n\r\n  /* Dialog Colors */\r\n  --dialog-bg: #2f3438;\r\n  --dialog-font-color: #e0e0e0;\r\n  --dialog-fiels-bg-color: #373c3f;\r\n  --dialog-fiels-bg-color-hover: #373c3f;\r\n  --dialog-button-bg-color: #373c3f;\r\n  --dialog-button-bg-color-hover: #3f4448;\r\n}\r\n\r\nhtml,\r\nbody {\r\n  height: 100%;\r\n  overflow: hidden;\r\n}\r\n\r\nhtml {\r\n  font-family: Lato, Arial, sans-serif;\r\n  font-weight: normal;\r\n}\r\n\r\n/* generic things */\r\nbody {\r\n  background-color: var(--bg-color);\r\n  display: flex;\r\n}\r\n\r\n.horizontal-vertical-centering {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\nmain {\r\n  display: flex;\r\n  flex-direction: column;\r\n  flex: 1;\r\n  padding: 1rem 0 0 0;\r\n  overflow: hidden;\r\n  background-color: var(--bg-color);\r\n}\r\n\r\n/* Sidebar */\r\n\r\n#menu-icon-span {\r\n  background-color: var(--bg-color);\r\n  flex-shrink: 0;\r\n}\r\n\r\n#menu-icon-wrapper {\r\n  transition: transform 0.3s ease-out;\r\n}\r\n\r\n.menu-icon {\r\n  border-radius: 3px;\r\n  color: var(--menu-icon-color);\r\n  cursor: pointer;\r\n  padding: 0.2rem;\r\n  top: 0;\r\n  position: sticky;\r\n}\r\n\r\n.menu-icon:hover {\r\n  background-color: var(--menu-icon-hover);\r\n}\r\n\r\n.rotate-y-axis {\r\n  transform: rotateY(180deg);\r\n}\r\n\r\n.container {\r\n  display: flex;\r\n  height: 100%;\r\n  overflow: hidden;\r\n  flex-grow: 1;\r\n}\r\n\r\n.sidebar {\r\n  background-color: var(--sidebar-bg-color);\r\n  padding: 0.5rem 0.3rem;\r\n  flex-basis: 15rem;\r\n  flex-shrink: 0;\r\n  overflow-y: auto;\r\n  color: var(--sidebar-font-color);\r\n  padding-block-start: 1rem;\r\n  box-shadow: rgba(255, 255, 255, 0.05) -1px 0px 0px 0px inset;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.sidebar.close {\r\n  flex-basis: 0;\r\n  padding: 0;\r\n}\r\n\r\n.sidebar:not(.close) ~ main .note-wrapper {\r\n  min-width: 18rem;\r\n}\r\n\r\n.sidebar-item {\r\n  text-decoration: none;\r\n  cursor: pointer;\r\n  border-radius: 10px;\r\n  color: inherit;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n}\r\n\r\n.sidebar-item,\r\n.project-header {\r\n  padding: 0.4rem 1rem;\r\n  border-radius: 10px;\r\n  transition: background-color 0.3s ease;\r\n}\r\n\r\n.sidebar-item:hover,\r\n.project-header:hover {\r\n  background-color: var(--sidebar-bg-color-hover);\r\n}\r\n\r\n.sidebar-item > svg {\r\n  min-width: 24px;\r\n  height: auto;\r\n  overflow: hidden;\r\n}\r\n\r\n.project-header {\r\n  display: grid;\r\n  grid-auto-flow: column;\r\n  align-items: center;\r\n  margin-block-start: 2rem;\r\n  font-size: 1rem;\r\n  font-weight: 300;\r\n\r\n  & button {\r\n    font-weight: normal;\r\n    border: none;\r\n    border-radius: 4px;\r\n    color: inherit;\r\n    background-color: transparent;\r\n    font-size: 1.6rem;\r\n    justify-self: end;\r\n    padding: 0.15rem;\r\n    cursor: pointer;\r\n\r\n    &:hover {\r\n      background-color: var(--project-add-button-bg-color);\r\n    }\r\n  }\r\n}\r\n\r\n.projects h2 {\r\n  white-space: nowrap;\r\n  text-overflow: ellipsis;\r\n  overflow: hidden;\r\n}\r\n\r\n.project-three-dots {\r\n  margin-inline-start: auto;\r\n  font-weight: normal;\r\n  border: none;\r\n  border-radius: 4px;\r\n  color: inherit;\r\n  background-color: transparent;\r\n  justify-self: end;\r\n  padding: 0.15rem;\r\n  cursor: pointer;\r\n  flex-shrink: 0;\r\n  position: relative;\r\n\r\n  &:hover {\r\n    background-color: var(--project-add-button-bg-color);\r\n  }\r\n}\r\n\r\n/* To do */\r\n\r\n.note-wrapper {\r\n  flex: 1;\r\n  overflow-y: auto;\r\n  overflow-x: hidden;\r\n  padding-inline-end: 10px;\r\n  padding-inline-start: 5px;\r\n  margin-block-end: 2rem;\r\n}\r\n\r\n.empty-message {\r\n  color: var(--sidebar-font-color);\r\n  font-size: 1.5rem;\r\n  text-align: center;\r\n  margin-block-start: 2rem;\r\n}\r\n\r\n.scroll-indicator-span {\r\n  --scroll-indicator-content: \"\";\r\n  position: relative;\r\n  text-align: center;\r\n  bottom: 1.5rem;\r\n  font-size: 2rem;\r\n  border-radius: 5px;\r\n  opacity: 0.65;\r\n  color: white;\r\n  transition: opacity 0.3s ease;\r\n}\r\n\r\n.scroll-indicator-span:hover {\r\n  opacity: 0.3;\r\n}\r\n\r\n.scroll-indicator-span::before {\r\n  content: var(--scroll-indicator-content);\r\n  position: absolute;\r\n  bottom: 1rem;\r\n  width: 2rem;\r\n  height: 2rem;\r\n  cursor: pointer;\r\n  animation: gentle-bounce 2.5s ease-in-out infinite;\r\n}\r\n\r\n@keyframes gentle-bounce {\r\n  0%, 100% {\r\n    transform: translateY(0);\r\n    opacity: 0.7;\r\n  }\r\n  50% {\r\n    transform: translateY(4px);\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n.add-todo-wrapper {\r\n  display: flex;\r\n  justify-content: end;\r\n  padding-inline-end: 1rem;\r\n  padding-block-end: 8px;\r\n}\r\n\r\n.add-todo-button {\r\n  font-size: 2rem;\r\n  color: white;\r\n  border-radius: 100%;\r\n  border: none;\r\n  background-color: var(--plus-button-color);\r\n  cursor: pointer;\r\n  transition: background-color 0.3s ease;\r\n  padding: 1rem;\r\n  flex-shrink: 0;\r\n}\r\n\r\n.add-todo-button:hover {\r\n  background-color: var(--plus-button-color-hover);\r\n}\r\n\r\ndialog {\r\n  border: none;\r\n  border-radius: 8px;\r\n  padding: 1.5rem;\r\n  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);\r\n  background-color: var(--dialog-bg);\r\n  color: var(--dialog-font-color);\r\n  width: min(100%, 65ch);\r\n\r\n  form {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 1rem;\r\n  }\r\n\r\n  input,\r\n  textarea,\r\n  select,\r\n  button {\r\n    padding: 0.5rem;\r\n    font-size: 1rem;\r\n    border: 1px solid #b7ada4;\r\n    border-radius: 4px;\r\n    background-color: var(--dialog-fiels-bg-color);\r\n    width: 100%;\r\n    color: white;\r\n\r\n    & + label {\r\n      margin-block-start: 0.5rem;\r\n    }\r\n  }\r\n\r\n  label {\r\n    font-weight: 700;\r\n    font-size: 1.2rem;\r\n  }\r\n\r\n  &::backdrop {\r\n    background: repeating-linear-gradient(\r\n      45deg,\r\n      rgba(0, 0, 0, 0.2),\r\n      rgba(0, 0, 0, 0.2) 1px,\r\n      rgba(0, 0, 0, 0.3) 1px,\r\n      rgba(0, 0, 0, 0.3) 20px\r\n    );\r\n    backdrop-filter: blur(3px);\r\n  }\r\n\r\n  textarea {\r\n    resize: vertical;\r\n  }\r\n\r\n  .dialog-buttons {\r\n    display: flex;\r\n    justify-content: end;\r\n    gap: 1rem;\r\n    margin-block-start: 1rem;\r\n\r\n    & button {\r\n      cursor: pointer;\r\n      background-color: var(--dialog-button-bg-color);\r\n      color: white;\r\n      border: none;\r\n      justify-self: end;\r\n      flex-grow: 0;\r\n      transition: background-color 0.3s ease;\r\n    }\r\n\r\n    & button:hover {\r\n      background-color: var(--dialog-button-bg-color-hover);\r\n    }\r\n  }\r\n\r\n  /* dialog checkbox */\r\n  .completed-label {\r\n    display: flex;\r\n    gap: 2rem;\r\n    align-items: center;\r\n  }\r\n\r\n  .completed-label .completed {\r\n    appearance: none;\r\n    background-color: #dfe1e4;\r\n    border-radius: 72px;\r\n    border-style: none;\r\n    flex-shrink: 0;\r\n    height: 1.25em;\r\n    margin: 0;\r\n    position: relative;\r\n    width: 1.875em;\r\n  }\r\n\r\n  .completed-label .completed::before {\r\n    bottom: -0.375em;\r\n    content: \"\";\r\n    left: -0.375em;\r\n    position: absolute;\r\n    right: -0.375em;\r\n    top: -0.375em;\r\n  }\r\n\r\n  .completed-label .completed,\r\n  .completed-label .completed::after {\r\n    transition: all 100ms ease-out;\r\n  }\r\n\r\n  .completed-label .completed::after {\r\n    background-color: rgb(50, 50, 50);\r\n    border-radius: 50%;\r\n    content: \"\";\r\n    height: 0.875em;\r\n    left: 0.1875em;\r\n    position: absolute;\r\n    top: 0.1875em;\r\n    width: 0.875em;\r\n  }\r\n\r\n  .completed-label input[type=\"checkbox\"] {\r\n    cursor: default;\r\n  }\r\n\r\n  .completed-label .completed:hover {\r\n    background-color: #9c9da0;\r\n    transition-duration: 0s;\r\n  }\r\n\r\n  .completed-label .completed:checked {\r\n    background-color: rgb(102, 102, 102);\r\n  }\r\n\r\n  .completed-label .completed:checked::after {\r\n    background-color: #fff;\r\n    left: 0.8125em;\r\n  }\r\n\r\n  .c